/* This section is used for the dropdown menu*/
body { 
  background: #ccc;
  font-family: helvetica, arial, serif;
  font-size: 13px;
  text-transform: uppercase;
  text-align: center;
}
 
.wrap {
  display: inline-block;
  -webkit-box-shadow: 0 0 70px #fff;
  -moz-box-shadow: 0 0 70px #fff;
  box-shadow: 0 0 70px #fff;
  margin-top: 0px;
}

/* a little "umph" */
.decor {
  background: #6EAF8D;
  background: -webkit-linear-gradient(to left, #CDEBDB 50%, #6EAF8D 50%);
  background: -moz-linear-gradient(to left, #CDEBDB 50%, #6EAF8D 50%);
  background: -o-linear-gradient(to left, #CDEBDB 50%, #6EAF8D 50%);
  background: linear-gradient(to left, white 50%, #6EAF8D 50%);
  background-size: 50px 25%;;
  padding: 2px;
  display: block;
}

a {
  text-decoration: none;
  color: #fff;
  display: block;
}

ul {
  list-style: none;
  position: relative;
  text-align: left;
  padding: 0;
}

li {
  float: left;
}

/* clear'n floats */
ul:after {
  clear: both;
}

ul:before,
ul:after {
    content: " ";
    display: table;
}

nav {
  position: relative;
  background: #2B2B2B;
  background-image: -webkit-linear-gradient(to bottom, #2B2B2B 7%, #333333 100%);
  background-image: -moz-linear-gradient(to bottom, #2B2B2B 7%, #333333 100%);
  background-image: -o-linear-gradient(to bottom, #2B2B2B 7%, #333333 100%);
  background-image: linear-gradient(to bottom, #2B2B2B 7%, #333333 100%);
  text-align: left;
  letter-spacing: 1px;
  text-shadow: 1px 1px 1px #0E0E0E;
  -webkit-box-shadow: 2px 2px 3px #888;
  -moz-box-shadow: 2px 2px 3px #888;
  box-shadow: 2px 2px 3px #888;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}

/* prime */
ul.primary li a {
  display: block;
  padding: 20px 30px;
  border-right: 1px solid #3D3D3D;
}

ul.primary li:last-child a {
  border-right: none;
}

ul.primary li a:hover {
  
  color: #000;
}

/* subs */
ul.sub {
  position: absolute;
  z-index: 200;
  box-shadow: 2px 2px 0 #BEBEBE;
  width: 35%;
  display:none;
}

ul.sub li {
  float: none;
  margin: 0;
}

ul.sub li a {
  border-bottom: 1px dotted #ccc;
  border-right: none;
  color: #000;
  padding: 15px 30px;
}

ul.sub li:last-child a {
  border-bottom: none;
}

ul.sub li a:hover {
  color: #000;
  background: #eeeeee;
}

/* sub display*/
ul.primary li:hover ul {
  display: block;
  background: #fff;
}

/* keeps the tab background white */
ul.primary li:hover a {
  background: #fff;
  color: #666;
  text-shadow: none;
}

ul.primary li:hover > a{
  color: #000;
} 

@media only screen and (max-width: 600px) {
  .decor {
    padding: 3px;
  }
  
  .wrap {
    width: 100%;
    margin-top: 0px;
  }
  
   li {
    float: none;
  }
  
  ul.primary li:hover a {
    background: none;
    color: #8B8B8B;
    text-shadow: 1px 1px #000;
  }

  ul.primary li:hover ul {
    display: block;
    background: #272727;
    color: #fff;
  }
  
  ul.sub {
    display: block;  
    position: static;
    box-shadow: none;
    width: 100%;
  }
  
  ul.sub li a {
    background: #272727;
    border: none;
    color: #8B8B8B;
  }
  
  ul.sub li a:hover {
    color: #ccc;
    background: none;
  }
}

/* The end of dropdown menu*/

/* Title text */
h1{
  font-family:Black Ops One ;
  font-size: 40px;
}
.link{color: black;}


/* Photo Gallery */
.bg1{
  background: url('https://i.ibb.co/dBLbrRV/bg1.jpg');

}


  

.pic{
  width: 100%;
  height:100%;
  object-fit: cover;

}

.container{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;

}
.container > div{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;

  cursor: pointer;
  height: 400px;
  background-size: cover;
  background-attachment: fixed;
  transition: all 0.5s ease-in;

}

.container>div:hover{
  opacity: 0.7;
  transform: scale(0.98);
}


.container > div:nth-of-type(1){
  grid-column: 1 / 3;
}

.container > div:nth-of-type(3){
  grid-column: 1 / 3;
}

.container > div:nth-of-type(5){
  grid-column: 1 / 3;
}


.container > div:nth-of-type(2){
  grid-column: 3 / 5;
}

.container > div:nth-of-type(4){
  grid-column: 3 / 5;
}
.container > div:nth-of-type(6){
  grid-column: 3 / 5;
}


